.input--group {
    .input-group-text {
      display: grid;
      place-items: center;
      padding-inline: 15px;
      position: relative;
      border: 1px solid hsl(var(--border) / 0.5);
      border-radius: 3px;
      background: hsl(var(--light-dark));
      font-size: 14px;
      font-weight: 500;
      color: hsl(var(--dark));
    }
  }