.footer {
    position: relative;
    isolation: isolate;
    background: hsl(var(--dark-600)) url(../images/footer-bg.png);
    background-size: cover;
    background-position: left center;
    background-repeat: no-repeat;
    
    &__copyright {
        background: hsl(var(--dark));
    }
    &__description {
        max-width: 50ch;
    }
    &__title {
        display: inline-block;
        position: relative;
        isolation: isolate;
        color: hsl(var(--white));
        &::after {
            content: '';
            position: absolute;
            inset: auto -5px 0 -5px;
            height: 40%;
            background: hsl(var(--accent)/.5);
            z-index: -1;
        }
    }
}
