.qr-code-wrapper {
    background: hsl(var(--white));
}

.qr-code {
    padding: 5px 10px;
    border: 1px solid hsl(var(--base)/0.15);
    border-radius: 5px;
    background: hsl(var(--base)/0.05);
    color: hsl(var(--dark));
}

.qr-code-copy-form {
    display: flex;

    input[type="text"] {
        background-color: transparent;
        border: none;
        font-size: 14px;
        width: calc(100% - 75px);
        height: 40px;
        @include iphn-plus {
            font-size: 1rem;
        }
    }
    .text-copy-btn {
        width: 75px;
        background-color: hsl(var(--base));
        border: none;
        border-radius: 5px;
        font-size: 14px;
    }
}
.qr-code-form {
    position: relative;
    .form--control {
        height: 65px;
        padding-right: 95px;
    }
    &__btn {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 75px;
        height: calc(100% - 20px);
        font-size: 14px;
    }
}