.select-lang {
    position: relative;
    isolation: isolate;
    display: flex;
    gap: 5px;
    margin-left: 15px;

    &::before {
        content: "\f107";
        font-family: "Line Awesome Free";
        font-weight: 900;
        color: hsl(var(--light));
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        height: 100%;
        display: grid;
        place-items: center;
        border-radius: 0 0.25rem 0.25rem 0;
        pointer-events: none;
        z-index: 1;
        font-size: 12px;

        @include lg-screen {
            padding-right: 0;
        }
    }

    &--container {
        padding-top: 3px;
        padding-bottom: 3px;
    }

    &__icon {
        color: hsl(var(--light));
    }

    .form-select {
        border: none;
        padding: 0;
        font-family: var(--body-font);
        font-size: 14px;
        font-weight: 500;
        color: hsl(var(--light));
        background: transparent;

        @include lg-screen {
            padding-right: 12px;
        }

        &:focus {
            box-shadow: none;
        }
    }

    option {
        padding-left: 10px;
        padding-right: 10px;
        background-color: hsl(var(--accent-dark));
    }
}