.facts {
    @include md-screen {
        flex-direction: row;
        justify-content: space-between;
    }

    &-content {
        &__title {
            display: block;
            font-family: var(--heading-font);
            font-size: 48px;
            font-weight: 700;
            line-height: 1.1;
            color: hsl(var(--heading));

            @include lg-screen {
                font-size: 30px;
            }

            @include xl-screen {
                font-size: 48px;
            }

            @include xlii-screen {
                font-size: 60px;
            }

            &-tail {
                display: inline-block;
                font-size: 20px;
            }
        }

        &__sub-title {
            display: inline-block;
            font-family: var(--heading-font);
            font-size: 18px;
            font-weight: 700;
            color: hsl(var(--heading));
        }
    }
}