.support-card {
    border: 1px solid hsl(var(--base)/.2);
    border-radius: 5px;
    background: hsl(var(--white));
    &__title {
        margin-top: 0;
        margin-bottom: 0.5rem;
        text-align: center;
        @include md-screen {
            margin-bottom: 0;
        }
    }
    &__head {
        padding: 12px 15px;
        border-bottom: 1px solid hsl(var(--base)/.2);
        @include md-screen {
            display: flex;
            justify-content: space-between;
        }
    }
    &__date {
        display: block;
        line-height: 1;
        text-align: center;
    }
    &__body {
        padding: 12px 15px;
        &-text {
            font-size: 14px;
            color: hsl(var(--dark)/.8);
        }
    }
    &__list {
       --gap: .5rem; 
       @include md-screen {
        margin-top: 1rem;
       }
    }
    &__file {
        display: flex;
        align-items: center;
        gap: 5px;
        padding: 7px 10px;
        border: 1px solid hsl(var(--base)/.2);
        border-radius: 5rem;
        background: hsl(var(--base)/0.1);
        font-size: 14px;
        line-height: 1;
        color: hsl(var(--base));
        text-decoration: none;
        transition: all .3s ease;
        &:hover {
            background: hsl(var(--base));
            color: hsl(var(--white));
        }
    }
}