.button {
    display: inline-block;
    text-decoration: none;
    transition: all .3s ease;

    &--login {
        padding: 8px 15px;
        border-radius: 2rem;
        background: hsl(var(--base));
        color: hsl(var(--white));
        font-size: 12px;
        line-height: 1;

        &:hover {
            background: hsl(var(--base-dark));
            color: hsl(var(--white));
        }

        &:focus {
            background: hsl(var(--base-dark));
            color: hsl(var(--white));
        }
    }

    &--light {
        padding: 6px 15px;
        background: hsl(var(--light));
        color: hsl(var(--base));
        font-size: 12px;

        &:hover {
            background: hsl(var(--light));
            color: hsl(var(--base));
        }
        &:focus {
            background: hsl(var(--light));
            color: hsl(var(--base));
        }
    }
    &--link-accent {
        display: flex;
        align-items: center;
        gap: 5px;
        padding-inline: 15px;
        color: hsl(var(--white));
        &:hover {
            color: hsl(var(--accent));
        }
        &:focus {
            color: hsl(var(--accent));
        }
        &__icon {
            font-size: 20px;
            line-height: 1;
        }
        &__text {
            font-size: 14px;
            line-height: 1;
        }
    }
}