.dashboard-menu {
    position: fixed;
    inset: 0;
    background: hsl(var(--dark));
    z-index: 1021;
    visibility: hidden;
    opacity: 0;
    transform: translateX(-100%);
    transition: all .3s ease;

    @include lg-screen {
        position: unset;
        inset: unset;
        visibility: visible;
        opacity: 1;
        transform: translateX(0);
        background: transparent;
    }

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid hsl((var(--light)/.3));
        background: hsl(var(--dark-600));

        @include lg-screen {
            display: none;
        }

        &-text {
            display: block;
            padding-left: 15px;
            font-size: 14px;
            line-height: 1.2;
            color: hsl(var(--light));
        }

        &-close {
            padding: 0;
            width: 40px;
            height: 40px;
            display: grid;
            place-content: center;
            font-size: 24px;
            color: hsl(var(--light));
            border: 0;
            border-left: 1px solid hsl((var(--light)/.3));
            border-radius: 0;

            &:hover {
                color: hsl(var(--light));
                border-left: 1px solid hsl((var(--light)/.3));
            }

            &:focus {
                color: hsl(var(--light));
                border-left: 1px solid hsl((var(--light)/.3));
            }
        }
    }

    &__body {
        max-height: calc(100vh - 41px);
        background: hsl(var(--dark-500));

        @include landscape {
            min-height: 320px;
        }

        @include lg-screen {
            min-height: auto;
            max-height: calc(100vh - 346px);
            background: transparent;
            border: 1px solid hsl(var(--border)/.5);
            border-radius: 10px;
        }
        @include xl-screen {
            max-height: unset;
        }

        .accordion-header {
            margin-top: 0;
        }

        .accordion-item:first-of-type .accordion-button {
            border-radius: 0;
            @include lg-screen {
                border-radius: 3px;
            }
        }

        .accordion-button:not(.collapsed) {
            background: hsl(var(--dark));
            color: hsl(var(--light));
            box-shadow: none;

            @include lg-screen {
                background: hsl(var(--white));
                box-shadow: none;
                color: hsl(var(--dark));
            }

            &::after {
                content: "\f106";
                transform: rotate(0);
            }

            &[aria-expanded="false"]::after {
                content: "\f107";
            }
        }

        .accordion-item:last-of-type {
            border-radius: 0;
        }

        .accordion-item {
            border: none;
            background: hsl(var(--dark-300));

            @include lg-screen {
                background: transparent;
            }
        }

        .accordion-button {
            display: flex;
            align-items: center;
            gap: 10px;
            padding: 10px 15px;
            background: hsl(var(--dark));
            font-family: var(--body-font);
            color: hsl(var(--light));
            font-size: 14px;
            line-height: 1.2;

            @include lg-screen {
                background: hsl(var(--white));
                color: hsl(var(--dark));
            }

            &:focus {
                box-shadow: none;
            }

            &:hover {
                color: hsl(var(--light));

                @include lg-screen {
                    color: hsl(var(--white));
                    background: hsl(var(--base));
                    .accordion-button__icon {
                        color: hsl(var(--white));
                    }
                }
            }

            &::after {
                content: "\f107";
                font-family: 'Line Awesome Free';
                font-weight: 900;
                background-image: unset;
            }
            &[aria-expanded="true"] {
                @include lg-screen {
                    background: hsl(var(--base));
                    color: hsl(var(--white));
                    .accordion-button__icon {
                        color: hsl(var(--white));
                    }
                }
            }

            &__icon {
                display: inline-block;
                flex-shrink: 0;
                font-size: 24px;
                line-height: 1;
                color: hsl(var(--base));
            }
        }

        .accordion-body {
            padding: 0;
            @include lg-screen {
                background: hsl(var(--light-100)/.5);
            }
        }

        .accordion.has-sub {
            .accordion-button {
                padding-left: 48px;
                padding-right: 15px;
                background: hsl(var(--dark-300));
                @include lg-screen {
                    background: hsl(var(--light));
                    color: hsl(var(--dark));
                }

                &:hover {
                    box-shadow: none;
                    color: hsl(var(--light));
                    @include lg-screen {
                        color: hsl(var(--base));
                    }
                }

                &:focus {
                    box-shadow: none;
                    color: hsl(var(--light));
                    @include lg-screen {
                        color: hsl(var(--base));
                    }
                }
            }
        }
    }

    &__list {
        --gap: 1px;
        background: hsl(var(--dark-100));

        @include lg-screen {
            background: transparent;
            --gap: 0;
            padding-top: 50px;
            padding-bottom: 15px;
            margin-left: 15px;
            margin-right: 15px;
        }

        li {
            background: hsl(var(--dark-600));

            @include lg-screen {
                background: transparent;

                &:last-child {
                    border-bottom: none;
                }
            }
        }
    }

    &__link {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 10px 15px;
        background: hsl(var(--dark));
        font-size: 14px;
        line-height: 1.2;
        color: hsl(var(--light));
        text-decoration: none;

        @include lg-screen {
            background: hsl(var(--white));
            color: hsl(var(--dark));
            border-radius: 3px;
        }

        &:hover {
            background: hsl(var(--base));
            color: hsl(var(--white));
            .dashboard-menu__icon {
                color: hsl(var(--white));
            }
        }
    }

    &__icon {
        display: inline-block;
        flex-shrink: 0;
        font-size: 24px;
        line-height: 1;
        color: hsl(var(--base));
    }

    &__text {
        display: block;
        flex-grow: 1;
    }

    &__inner {
        --gap: 0;

        >li {
            background: hsl(var(--dark-300));
            border-bottom: 1px solid hsl(var(--light)/.1);
            @include lg-screen {
                background: hsl(var(--dark)/.1);
                border-bottom: 1px solid hsl(var(--base)/.1);

            }

            &:last-child {
                border-bottom: none;
            }
        }

        &-link {
            display: block;
            padding: 10px 10px 10px 48px;
            font-size: 14px;
            line-height: 1.2;
            color: hsl(var(--light));
            text-decoration: none;
            @include lg-screen {
                color: hsl(var(--dark));
                background: hsl(var(--light));
            }

            &:hover {
                color: hsl(var(--base));
            }
        }
    }
}