.breadcrumbs {
  justify-content: center;
  align-items: center;
  --gap: 2rem;

  &__item {
    display: flex;
    align-items: center;
    position: relative;

    &::after {
      content: "";
      position: relative;
      display: inline-block;
      right: -1rem;
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background-color: hsl(var(--base));
    }

    &:last-child {
      margin-right: 0;

      &::after {
        display: none;
      }
    }
  }

  &__link {
    color: hsl(var(--white));

    &:hover {
      color: hsl(var(--base));
    }
  }
}