@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500&family=Plus+Jakarta+Sans:wght@300;400;500;600;700&display=swap');

:root {
  // TypoGraphy
  --heading-font: 'Plus Jakarta Sans', sans-serif;
  --body-font: 'Open Sans', sans-serif;

  --h1: clamp(2.1rem, 4vw + 1rem, 3.052rem); //Min:33.6px - Max:42.72px
  --h2: clamp(1.69775rem, 3vw + 1rem, 2.441rem); //Min:27.164px - Max: 34.18px
  --h3: clamp(1.367rem, 2.7vw + 1rem, 1.953rem); //Min:21.872px - Max: 27.34px
  --h4: clamp(1.2504rem, 2vw + 1rem, 1.563rem); //20.0064 - 25.008px
  --h5: 1.25rem; //20px
  --h6: 1rem;

  // Base Colors
  --base-h: 24;
  --base-s: 97%;
  --base-l: 54%;
  --base: var(--base-h) var(--base-s) var(--base-l);
  --base-light: calc(var(--base-h) - 3) calc(var(--base-s) - 5%) calc(var(--base-l) + 5%);
  --base-dark: calc(var(--base-h) - 3) calc(var(--base-s) - 5%) calc(var(--base-l) - 6%);

  // Accent Color 
  --accent-h: 227;
  --accent-s: 60%;
  --accent-l: 40%;
  --accent: var(--accent-h) var(--accent-s) var(--accent-l);
  --accent-light: calc(var(--accent-h) - 3) calc(var(--accent-s) - 5%) calc(var(--accent-l) + 5%);
  --accent-dark: var(--accent-h) var(--accent-s) calc(var(--accent-l) - 8%);

  //Light Color
  --light-h: 240;
  --light-s: 100%;
  --light-l: 98%;
  --light: var(--light-h) var(--light-s) var(--light-l);
  --light-dark: var(--light-h) var(--light-s) calc(var(--light-l) - 2%);

  //Dark Color
  --dark-h: 237;
  --dark-s: 57%;
  --dark-l: 15%;
  --dark: var(--dark-h) var(--dark-s) var(--dark-l);
  --dark-100: var(--dark-h) calc(var(--dark-s) - 7%) calc(var(--dark-l) + 15%);
  --dark-200: var(--dark-h) calc(var(--dark-s) - 7%) calc(var(--dark-l) + 10%);
  --dark-300: var(--dark-h) calc(var(--dark-s) - 7%) calc(var(--dark-l) + 8%);
  --dark-400: var(--dark-h) calc(var(--dark-s) - 7%) calc(var(--dark-l) + 5%);
  --dark-500: var(--dark-h) var(--dark-s) calc(var(--dark-l) - 2%);
  --dark-600: var(--dark-h) var(--dark-s) calc(var(--dark-l) - 4%);
  --dark-700: var(--dark-h) var(--dark-s) calc(var(--dark-l) - 6%);
  --dark-800: var(--dark-h) var(--dark-s) calc(var(--dark-l) - 8%);

  // Black Color Shade 
  --black-h: 0;
  --black-s: 0%;
  --black-l: 0%;
  --black: var(--black-h) var(--black-s) var(--black-l);

  // White Color 
  --white: 0 0% 100%;

  //Bootstrap Modifier
  --primary: 204 88% 44%;
  --secondary: 208 23% 55%;
  --success: 161 74% 48%;
  --danger: 5 76% 55%;
  --warning: 40 99% 57%;
  --info: 190 76% 40%;

  --text-h: 207;
  --text-s: 22%;
  --text-l: 48%;
  --text: var(--text-h) var(--text-s) var(--text-l);
  --heading-h: 206;
  --heading-s: 23%;
  --heading-l: 28%;
  --heading: var(--heading-h) var(--heading-s) var(--heading-l);
  --border: 205 22% 85%;
}

html {
  font-size: 100%;
}

body {
  position: relative;
  background: hsl(var(--white));
  font-family: var(--body-font);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.75;
  color: hsl(var(--text));
}

// Typography
p {
  margin-bottom: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1.5rem 0 1rem;
  font-family: var(--heading-font);
  font-weight: 700;
  line-height: 1.2;
  color: hsl(var(--heading));

}

h1 {
  margin-top: 0;
  font-size: var(--h1);
}

h2 {
  font-size: var(--h2);
}

h3 {
  font-size: var(--h3);
}

h4 {
  font-size: var(--h4);
}

h5 {
  font-size: var(--h5);
}

h6 {
  font-size: var(--h6);
  letter-spacing: .05em;
}

.xsm-text {
  font-size: .75rem;
}

small,
.sm-text {
  font-size: .875rem;
}

.lg-text {
  font-size: 1.125rem;
}

.xl-text {
  font-size: 1.25rem;
}

.xxl-text {
  font-size: 1.5rem;
}

.fw-regular {
  font-weight: 400;
}

.fw-md {
  font-weight: 500;
}

.lh-1 {
  line-height: 1;
}

.hr {
  background-color: hsl(var(--base));
}

// Button Default
button {
  &:focus {
    outline: none !important;
  }
}

.btn:focus,
.btn.focus {
  outline: none;
  box-shadow: none;
}

// Form Control Default
input:focus {
  outline: none;
}

.form-label {
  font-family: var(--heading-font);
  font-size: 14px;
  font-weight: 600;
  color: hsl(var(--heading));
}

textarea {
  resize: none;
}

.container-restricted {
  @include xliv-screen {
    max-width: 1540px;
    margin-left: auto;
    margin-right: auto;
  }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}