.pricing {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    height: 100%;
    padding: 20px 15px;
    border-radius: 10px;
    border: 1px solid hsl(var(--border)/.5);
    background: hsl(var(--white));
    transition: all .3s ease;

    @include lg-screen {
        padding-top: 25px;
        padding-bottom: 25px;
    }
    &:hover {
        border-color: hsl(var(--accent)/.4);
    }

    &--popular {
        position: relative;
        border: 1px solid hsl(var(--base));
        &:hover {
            border-color: hsl(var(--base));
        }
    }

    &__title {
        margin-top: 0;
        margin-bottom: 5px;
        color: hsl(var(--dark));
    }

    &__sub-title {
        display: block;
        font-size: 14px;
        font-weight: 500;
        color: hsl(var(--dark));
    }

    &__amount {
        margin: 0;
        line-height: 1;
        color: hsl(var(--dark));
    }

    &__price {
        display: flex;
        align-items: center;
    }

    &__currency {
        display: inline-block;
        color: hsl(var(--dark));
        font-size: 18px;
    }

    &__plan-container {
        display: flex;
        align-items: flex-end;
        gap: 5px;
        margin-bottom: 1rem;
    }

    &__text {
        display: inline-block;
        font-family: var(--heading-font);
        font-size: 14px;
        font-weight: 600;
        color: hsl(var(--dark));
    }

    &__btn {
        width: 100%;
        padding: 10px 15px;
        border: none;
        border-radius: 5px;
        font-family: var(--heading-font);
        font-size: 12px;
        font-weight: 700;
        letter-spacing: .03em;
        transition: all .3s ease;

        &-regular {
            color: hsl(var(--dark));
            background: hsl(var(--light-dark));

            &:hover {
                color: hsl(var(--white));
                background: hsl(var(--accent));
            }
        }
        &-popular {
            color: hsl(var(--base-dark));
            background: hsl(var(--base)/.3);

            &:hover {
                color: hsl(var(--white));
                background: hsl(var(--base));
            }
        }
    }

    &__notification {
        margin-top: 5px;
        display: block;
        font-family: var(--heading-font);
        font-size: 12px;
        font-weight: 600;
        color: hsl(var(--dark));
        text-align: center;
    }

    &__list {
        --gap: .8rem;
    }

    &__item {
        display: flex;
        align-items: center;
        font-family: var(--heading-font);
        font-size: 14px;
        line-height: 1.2;
        color: hsl(var(--dark));

        &::before {
            display: grid;
            place-content: center;
            width: 20px;
            height: 20px;
            flex-shrink: 0;
            margin-right: 8px;
            border-radius: 50%;
            font-family: 'Line Awesome Free';
            font-weight: 900;
            font-size: 12px;
            line-height: 1;

        }

        &-success {
            &::before {
                content: "\f00c";
                background: hsl(var(--success)/.15);
                color: hsl(var(--info));
            }
        }

        &-danger {
            &::before {
                content: "\f00d";
                background: hsl(var(--danger)/.15);
                color: hsl(var(--danger));
            }
        }
    }
    &__tag {
        display: inline-block;
        padding: 5px 15px;
        position: absolute;
        top: -11px;
        right: 15px;
        border-radius: 1rem;
        background: hsl(var(--base));
        font-family: var(--heading-font);
        font-size: 12px;
        font-weight: 500;
        line-height: 1;
        letter-spacing: .04em;
        color: hsl(var(--white));
    }
}