.btn {
  font-family: var(--heading-font);
  font-weight: 500;
  transition: all 0.3s ease;
  border-radius: 3px;
  flex-shrink: 0;

  &:hover {
    box-shadow: 0 5px 10px hsl(var(--dark) / 0.1);
  }

  // Color Option
  &--primary {
    background: hsl(var(--primary));
    color: hsl(var(--white));

    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--primary));
    }
  }

  &--secondary {
    background: hsl(var(--secondary));
    color: hsl(var(--white));

    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--secondary));
    }
  }

  &--success {
    background: hsl(var(--success));
    color: hsl(var(--white));

    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--success));
    }
  }

  &--danger {
    background: hsl(var(--danger));
    color: hsl(var(--white));

    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--danger));
    }
  }

  &--warning {
    background: hsl(var(--warning));
    color: hsl(var(--white));

    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--warning));
    }
  }

  &--info {
    background: hsl(var(--info));
    color: hsl(var(--white));

    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--info));
    }
  }

  &--dark {
    background: hsl(var(--dark));
    color: hsl(var(--white));

    &:hover {
      color: hsl(var(--white));
      background: hsl(var(--dark));
    }
  }

  &--light {
    background-color: hsl(var(--white));
    color: hsl(var(--base));

    &:hover {
      border: 1px solid transparent;
      background: hsl(var(--light));
      color: hsl(var(--accent));
    }
  }

  &--base {
    overflow: hidden;
    position: relative;
    isolation: isolate;
    border-radius: 2rem;
    background: hsl(var(--base));
    font-size: 14px;
    color: hsl(var(--white));

    &:hover {
      border: 1px solid transparent;
      background: hsl(var(--base-dark));
      color: hsl(var(--white));
    }
    &:focus {
      border: 1px solid transparent;
      background: hsl(var(--base-dark));
      color: hsl(var(--white));
    }
    &:action {
      border: 1px solid transparent;
      background: hsl(var(--base-dark));
      color: hsl(var(--white));
    }
  }
  &--accent {
    overflow: hidden;
    position: relative;
    isolation: isolate;
    background: hsl(var(--accent));
    color: hsl(var(--white));

    &:hover {
      border: 1px solid transparent;
      background: hsl(var(--accent-dark));
      color: hsl(var(--white));
    }
    &:focus {
      border: 1px solid transparent;
      background: hsl(var(--accent-dark));
      color: hsl(var(--white));
    }
    &:action {
      border: 1px solid transparent;
      background: hsl(var(--accent-dark));
      color: hsl(var(--white));
    }
  }

  // Button  Shape
  &--sqr {
    line-height: 1;
    padding: 0.75rem;
    font-size: 20px;
  }
  &--circle {
    display: grid;
    place-content: center;
    width: 47px;
    height: 47px;
    padding: 0;
    border-radius: 50%;
    line-height: 1;
  }

  // Button Size
  &--sm {
    padding: 0.3rem 1rem; //8px X 16px
  }

  &--md {
    padding: 0.5rem 1rem; //8px X 16px
  }

  &--lg {
    padding: 0.625rem 1.25rem; //10px X 20px
  }

  &--xl {
    padding: 0.75rem 1.5rem; //12px X 24px
  }

  &--xxl {
    padding: 1rem 2rem; //16px X 24px
  }
  &--view {
    padding: 0;
    display: inline-grid;
    place-content: center;
    width: 30px;
    height: 30px;
    background: hsl(var(--base));
    font-size: 14px;
    line-height: 1;
    color: hsl(var(--light));
    &:hover {
      box-shadow: none;
      color: hsl(var(--light));
      background: hsl(var(--base));
    }
    &:focus {
      box-shadow: none;
      color: hsl(var(--light));
      background: hsl(var(--base));
    }
    &:active {
      box-shadow: none;
      color: hsl(var(--light));
      background: hsl(var(--base));
    }
  }
  &--add-more {
    min-width: 91px;
    height: 100%;
    border: none;
    font-size: 14px;
    line-height: 1;
    &:hover {
      box-shadow: none;
    }
  }
}