.contact-form {
    .form--control {
        border: 1px solid hsl(var(--border) / 0.3);
        background: hsl(var(--light)/.1);
    }

    .form-select {
        border: 1px solid hsl(var(--border) / 0.3);
        background: hsl(var(--light)/.1);
    }

    textarea.form--control {
        border: 1px solid hsl(var(--border) / 0.3);
        background: hsl(var(--light)/.1);

        &:focus {
            border: 1px solid hsl(var(--border) / 0.3);
            background: hsl(var(--light)/.1);
        }
    }
}