.hero {
  display: flex;
  flex-direction: column;
  position: relative;
  isolation: isolate;
  background-image: url(../images/hero-bg.png), linear-gradient(45deg, hsl(var(--accent-dark)), hsl(var(--accent)));
  background-size: cover;
  background-position: center bottom;
  background-repeat: no-repeat;
  z-index: 2;

  @include md-screen {
    min-height: 85vh;
  }


  &__img {
    position: relative;

    &-is {
      @include xliv-screen {
        width: 750px;
        max-width: 750px;
        height: 750px;
        object-fit: contain;
      }
    }
  }

  &__content {
    padding-top: 100px;
    padding-bottom: 80px;
    margin-top: auto;
    margin-bottom: auto;
    overflow: hidden;

    &-title {
      color: hsl(var(--white));
    }

    &-para {
      max-width: 55ch;
      font-size: 18px;
      color: hsl(var(--white));
    }
  }
  &__btn-group {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
  }
}