.service-section {
    position: relative;
    isolation: isolate;
    background: hsl(var(--dark));
    &::before {
        content: '';
        width: 150px;
        height: 150px;
        position: absolute;
        left: 0;
        top: 0;
        border-radius: 50%;
        filter: blur(50px);
        background: hsl(var(--danger)/.2);
        z-index: -1;
    }
    &::after {
        content: '';
        width: 400px;
        height: 400px;
        position: absolute;
        right: 0;
        bottom: 0;
        border-radius: 50%;
        filter: blur(80px);
        background: hsl(var(--base)/.2);
        background: linear-gradient(45deg, hsl(var(--base)/.2), hsl(var(--danger)/.2));
        z-index: -1;
    }

    .section__title {
        color: hsl(var(--light));
    }

    .section__subtitle {
        color: hsl(var(--base-light));

        &::before {
            background-color: hsl(var(--base-light));
        }

        &::after {
            background-color: hsl(var(--base-light)/.5);
        }
    }
}