.client-slider {
    &:hover {
        .client-slider__arrow {
            visibility: visible;
            opacity: 1;
        }
    }

    &__item {
        padding-left: .5rem;
        padding-right: .5rem;
        height: 60px;

        &:hover {
            .client-slider__img {
                filter: none;
            }
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        filter: grayscale(1);
        transition: all .3s ease;
        cursor: pointer;
    }

    &__arrow {
        display: grid;
        place-content: center;
        visibility: hidden;
        opacity: 0;
        padding: 0;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: 1px solid hsl(var(--accent));
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background: hsl(var(--light));
        color: hsl(var(--accent));
        line-height: 1;
        transition: all .3s ease;
        z-index: 1;

        &:hover {
            background: hsl(var(--accent));
            color: hsl(var(--light));
        }

        &-prev {
            left: 0;
        }

        &-next {
            right: 0;
        }
    }
}