.login-form {
    padding: 20px 15px;
    border-radius: 5px;
    background: hsl(var(--white));
    box-shadow: 0 0 15px hsl(var(--dark)/.05);

    @include sm-screen {
        padding: 30px 20px;
    }

    @include md-screen {
        padding: 50px 40px;
    }

    @include xl-screen {
        border-radius: 10px;
    }

    &__title {
        margin-top: 0;
        display: inline-block;
        position: relative;
        isolation: isolate;

        &::after {
            content: '';
            position: absolute;
            inset: auto -3px 0 -3px;
            height: 25%;
            background: hsl(var(--base)/.3);
            z-index: -1;
        }
    }
}