.recent-post {
    display: flex;
    gap: 1rem;
    &__img {
        width: 80px;
        height: 80px;
        flex-shrink: 0;
        border-radius: 5px;
        overflow: hidden;
        &-is {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__title {
        margin-top: 0;
        margin-bottom: 5px;
        &-link {
            color: hsl(var(--heading));
            text-decoration: none;
            transition: all .3s ease;
            &:hover {
                color: hsl(var(--base));
            }
        }
    }
    &__date {
        display: block;
        font-size: 14px;
    }
}