.section {
	padding-top: clamp(60px, 8vw, 120px);
	padding-bottom: clamp(60px, 8vw, 120px);

	&--sm {
		padding-top: clamp(30px, 4vw, 60px);
		padding-bottom: clamp(30px, 4vw, 60px);
	}

	&--top {
		padding-top: clamp(60px, 8vw, 120px);
	}

	&--bottom {
		padding-bottom: clamp(60px, 8vw, 120px);
	}

	&__head {
		padding-bottom: clamp(30px, 4vw, 60px);
	}

	&__para {
		max-width: 50ch;
	}

	&__title {
		span {
			display: inline-block;
			position: relative;
			isolation: isolate;

			img {
				position: absolute;
				width: 105%;
				bottom: 0;
				left: 50%;
				transform: translateX(-50%);
				z-index: -1;
				opacity: .8;
			}
		}
	}

	&__subtitle {
		display: inline-block;
		position: relative;
		font-size: 14px;
		font-weight: 500;
		letter-spacing: 0.05em;
		font-family: var(--heading-font);
		color: hsl(var(--accent));

		&::after {
			content: "";
			position: absolute;
			width: 10px;
			height: 4px;
			border-radius: 20px;
			top: 50%;
			left: calc(100% + 10px);
			transform: translateY(-50%);
			background-color: hsl(var(--accent) / 0.5);
		}

		&::before {
			content: "";
			position: absolute;
			width: 20px;
			height: 4px;
			border-radius: 20px;
			top: 50%;
			left: calc(100% + 25px);
			transform: translateY(-50%);
			background-color: hsl(var(--accent));
		}
	}
}