.support-ticket {
    border: 1px solid hsl(var(--base)/0.15);
    .card-header {
        background: hsl(var(--base)/.05);
        border-bottom: 1px solid hsl(var(--base)/0.15);
    }
    .card-footer {
        background: hsl(var(--base)/.05);
        border-top: 1px solid hsl(var(--base)/0.15);
    }
}