.dashboard-submenu {
    --gap: 0;

    >li {
        border-bottom: 1px solid hsl(var(--light)/.1);

        &:last-child {
            border-bottom: none;
        }
    }

    &__link {
        display: flex;
        padding: 8px 10px 8px 48px;
        font-size: 13px;
        color: hsl(var(--light));
        transition: all .3s ease;
        text-decoration: none;
        @include lg-screen {
            background: hsl(var(--light-100));
            color: hsl(var(--dark));
        }
        &:hover {
            color: hsl(var(--base));
        }

    }
}