.custom--blockquote {
    padding: 20px 15px;
    border-left: 5px solid hsl(var(--base));
    background: hsl(var(--light));
    @include xs-screen {
        padding: 30px 20px;
    }
    @include md-screen {
        padding: 35px 30px;
    }
    &__icon {
        display: block;
        margin-bottom: .5rem;
        @include xs-screen {
            margin-bottom: 1rem;
        }
    }
    &__text {
        margin-bottom: 0;
        font-size: 14px;
        font-weight: 400;
        font-style: italic;
        @include lg-screen {
            font-size: 16px;
        }
        @include xl-screen {
            font-size: 18px;
        }
    }
}