.dashboard-sidebar {
    @include lg-screen {
        position: sticky;
        top: 90px;
    }
    &__nav-toggle {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 8px 10px;
        margin-bottom: 15px;
        border-radius: 5px;
        background: hsl(var(--base)/.2);
        @include lg-screen {
            display: none;
        }

        &-text {
            display: block;
            font-size: 14px;
            line-height: 1.2;
            color: hsl(var(--dark));
            font-weight: 500;
        }

        &-btn {
            padding: 0;
            border: none;
            line-height: 1;
            font-size: 24px;
            background: transparent;
            color: hsl(var(--base-dark));

            &:hover {
                border: none;
                color: hsl(var(--base-dark));
                box-shadow: none;
            }

            &:focus {
                border: none;
                box-shadow: none;
                color: hsl(var(--base-dark));
            }
        }
    }
}