.sidebar-nav {
    li {
        &:first-child {
            .sidebar-nav__link {
                padding-top: 15px;
            }
        }
        &:last-child {
            .sidebar-nav__link {
                padding-bottom: 15px;
            }
        }
    }
    &__head {
        padding: 30px 15px 10px;
    }
    &__title {
        margin: 0;
    }
    &__link {
        display: block;
        padding: 5px 15px;
        border-right: 3px solid transparent;
        font-size: 14px;
        color: hsl(var(--text));
        font-weight: 500;
        transition: all 0.3s ease;

        &:hover {
            color: hsl(var(--base));
            border-right: 3px solid hsl(var(--base));
        }

        &.active {
            border-right: 3px solid hsl(var(--base));
            color: hsl(var(--base));
        }
    }
}