.sidebar {
    box-shadow: 0 0 5px hsl(var(--black)/.1);
    border: 1px solid hsl(var(--border)/.5);
    border-radius: 5px;
    &__head {
        padding: 15px;
        background: hsl(var(--base));
        border-radius: 5px 5px  0 0;
        @include md-screen {
            padding: 20px 30px;
        }
        @include lg-screen {
            padding: 15px;
        }
        @include xlii-screen {
            padding: 15px 30px;
        }
        &-title {
            margin: 0;
            color: hsl(var(--white));
        }
    }
    &__body {
        padding: 15px;
        @include md-screen {
            padding: 30px 40px;
        }
        @include lg-screen {
            padding: 15px;
        }
        @include xlii-screen {
            padding: 30px;
        }
    }
}