.list {
	display: flex;
	flex-direction: column;
	gap: var(--gap, 1rem);
	margin: 0;
	padding: 0;
	list-style: none;

	&--row {
		flex-direction: row;
	}

	&--base {
		>li {
			position: relative;
			display: flex;
			align-items: center;

			&::before {
				content: "";
				display: inline-flex;
				align-items: center;
				justify-content: center;
				flex-shrink: 0;
				width: 10px;
				height: 10px;
				line-height: 10px;
				border-radius: 50%;
				margin-right: 15px;
				background: hsl(var(--base));
				box-shadow: 0 0 0 5px hsl(var(--base)/.2);
			}
		}
	}

	&--check {
		>li {
			position: relative;
			display: flex;
			align-items: center;

			&::before {
				content: "\f00c";
				font-family: "Font Awesome 5 Free";
				font-weight: 900;
				color: hsl(var(--success));
				display: inline-block;
				flex-shrink: 0;
				margin-right: 8px;
			}
		}
	}
}