.blog-post {
    height: 100%;
    border: 1px solid hsl(var(--border)/.5);
    border-radius: 10px;

    &__img {
        display: block;
        width: 100%;
        height: 250px;
        border-radius: 10px 10px 0 0;
        text-decoration: none;
        overflow: hidden;

        &-is {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__body {
        padding: 30px 15px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
        border-radius: 0 0 10px 10px;
        background: hsl(var(--white));

        @include iphn-plus {
            padding-left: 20px;
            padding-right: 20px;
        }
        @include md-screen {
            padding: 30px;
        }
        @include lg-screen {
            padding: 30px 20px;
        }
        @include xl-screen {
            padding: 30px;
        }
    }
    &__title {
        margin: 0;
    }
    &__description {
        margin: 0;
        font-size: 14px;
    }
    &__info {
        display: flex;
        align-items: center;
        font-family: var(--heading-font);
        font-size: 13px;
        font-weight: 600;
        &-icon {
            font-size: 16px;
            line-height: 1;
        }
    }
    &__link {
        display: block;
        text-decoration: none;
        color: hsl(var(--heading));
        transition: all .3s ease;
        &:hover {
            color: hsl(var(--base));
        }
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 12px 24px;
        border-radius: 2rem;
        font-size: 14px;
        line-height: 1;
        text-decoration: none;
        border: 1px solid hsl(var(--base)/.15);
        background: hsl(var(--base)/.08);
        font-family: var(--heading-font);
        color: hsl(var(--base));
        font-weight: 600;
        transition: all .3s ease;
        &:hover {
            background: hsl(var(--base));
            color: hsl(var(--light));
        }
    }
    &__arrow {
        margin-left: 5px;
        font-size: 16px;
        line-height: 1;
    }
}