.deposit-card {
    padding: 15px;
    border-radius: 5px;
    background: hsl(var(--base)/.1);
    &__list {
        --gap: 0;
        li {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 15px;
            padding-top: 1.1rem;
            padding-bottom: 1.1rem;
            font-size: 14px;
            line-height: 1;
            border-bottom: 1px dashed hsl(var(--base)/.5);
            &:first-child {
                padding-top: 0;
            }
            &:last-child {
                padding-bottom: 0;
                border-bottom: none;
            }
        }
    }
    &__title {
        color: hsl(var(--heading));
    }
    &__amount {
        font-weight: 500;
        color: hsl(var(--heading));
    }
}