.header-menu {
    gap: .5rem;
    align-items: center;

    &__end {
        margin-left: auto;
    }

    &__link {
        display: flex;
        align-items: center;
        gap: .5rem;
        padding: 5px 10px;
        text-decoration: none;
        transition: all .3s ease;
    }

    &__icon {
        display: inline-block;
        color: hsl(var(--base));
    }

    &__text {
        display: none;
        font-size: 14px;
        color: hsl(var(--light));

        @include md-screen {
            display: block;
        }
    }
}