.dashboard-widget {
    padding: 20px 15px;
    display: flex;
    position: relative;
    isolation: isolate;
    box-shadow: 0 0 18px hsl(var(--dark)/.03);
    border: 1px solid hsl(var(--border)/.3);
    border-radius: 10px;
    background: hsl(var(--white));

    &__icon {
        width: 40px;
        height: 40px;
        display: grid;
        place-content: center;
        flex-shrink: 0;
        border-radius: 3px;
        background: hsl(var(--base));
        font-size: 20px;
        color: hsl(var(--white));
    }

    &__amount {
        margin-top: 5px;
        margin-bottom: 0;
    }

    &__content {
        padding-left: 15px;
    }

    &__overlay-icon {
        position: absolute;
        right: 15px;
        bottom: 15px;
        font-size: 50px;
        line-height: 1;
        z-index: -1;
        color: hsl(var(--base)/.05);
    }
}