.login-btn {
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 3px 15px;
    background: hsl(var(--base));
    font-size: 14px;
    font-weight: 600;
    color: hsl(var(--dark)/.7);
    text-decoration: none;
    transition: all 0.3s ease;
    
    &:hover {
        color: hsl(var(--dark-700));
    }
}