.contact-info {
    padding: 15px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 58px;
    border: 1px solid hsl(var(--border)/.3);
    border-radius: 10px;
    box-shadow: 0 0 10px hsl(var(--accent)/.05);
    background: hsl(var(--white));
    @include md-screen {
        padding: 30px;
        margin-top: 45px;
    }
    @include xl-screen {
        padding: 40px;
        margin-top: 35px;
    }

    &__icon {
        width: 92px;
        height: 92px;
        display: grid;
        place-content: center;
        flex-shrink: 0;
        position: relative;
        isolation: isolate;
        margin-top: -65px;
        outline: 8px solid hsl(var(--white));
        border-radius: 50%;
        box-shadow: 0 0 25px hsl(var(--accent-dark)/.3);
        background: hsl(var(--white));
        color: hsl(var(--base)/.7);
        font-size: 40px;
        line-height: 1;
        &::after {
            content: '';
            position: absolute;
            inset: 0;
            border-radius: 50%;
            background: hsl(var(--base)/.2);
            z-index: -2;
        }
    }
}