.testimonial {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 20px 15px;
    box-shadow: 0 0 8px hsl(var(--dark)/.1);
    border-radius: 10px 10px 0 0;
    transition: all .3s ease;
    border-bottom: 6px solid;
    border-image-slice: 1;
    border-bottom-width: 3px;
    border-image-source: linear-gradient(45deg, hsl(var(--danger)), hsl(var(--base)/.5));

    @include iphn-plus {
        padding: 30px 20px;
    }

    @include xlii-screen {
        padding: 40px 30px;
    }

    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 1rem;
    }
    &__subject {
        margin: 0;
        flex-grow: 1;
    }

    &__user {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 1rem;
        flex-grow: 1;
    }

    &__quote {
        flex-shrink: 0;
        font-size: 34px;
        line-height: 1;
        background: -webkit-linear-gradient(45deg, hsl(var(--danger)), hsl(var(--base)/.8));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    &__img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        flex-shrink: 0;
        overflow: hidden;

        &-is {
            width: 100%;
            height: 100%;
        }
    }

    &__title {
        margin-top: 0;
        margin-bottom: 5px;
        color: hsl(var(--dark));
    }

    &__sub-title {
        display: block;
        font-family: var(--heading-font);
        font-size: 14px;
        line-height: 1;
        font-weight: 500;
    }

    &__text {
        margin-bottom: 0;
        font-size: 14px;
    }
}