.fact-section {
    position: relative;
    isolation: isolate;
    background-color: hsl(var(--light));
    background-image: url(../images/fact-bg.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    &::after {
        content: '';
        position: absolute;
        inset: 0;
        background: hsl(var(--dark));
        z-index: -1;
        mix-blend-mode: color;
    }
}