.profile {
    display: none;
    position: relative;
    @include lg-screen {
        display: block;
    }
    &__bg {
        width: 100%;
        height: 100px;
        border-radius: 10px 10px 0 0;
        background-color: hsl(var(--base));
        background-image: url(../images/profile-bg.png);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    &__user {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        overflow: hidden;
        border: 5px solid hsl(var(--white));
        position: absolute;
        left: 50%;
        bottom: -40px;
        transform: translateX(-50%);
        z-index: 1;
    }
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}