.service-card {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 15px;
    position: relative;
    isolation: isolate;
    border-bottom: 3px solid hsl(var(--base)/.7);
    background-color: hsl(var(--dark-700));
    transition: all .3s ease;

    @include sm-screen {
        padding: 30px;
    }

    &:hover {
        border-color: hsl(var(--base));
        &::after {
            opacity: .4;
        }
    }

    &::after {
        content: '';
        width: 100px;
        aspect-ratio: 1;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        background: linear-gradient(45deg, hsl(var(--base)), hsl(var(--danger)));
        filter: blur(50px);
        z-index: -1;
        opacity: 0;
        transition: all .3s ease;
    }

    &__icon {
        font-size: 48px;
        line-height: 1;
        background: -webkit-linear-gradient(hsl(var(--warning)), hsl(var(--base)));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    &__title {
        margin-top: 0;
        color: hsl(var(--light));
    }

    &__para {
        margin-bottom: 0;
        font-size: 14px;
        color: hsl(var(--light)/.8);
    }
}