.map-section {
    position: relative;
    isolation: isolate;
    margin-bottom: clamp(-30px, -4vw, -60px);

    @include lg-screen {
        margin-bottom: clamp(-60px, -4vw, -90px);
    }

    &::after {
        content: '';
        position: absolute;
        inset: 0;
        z-index: -1;
        background: hsl(var(--light));
    }
}